.top{
    width: 100%;
    height: 80px;
    background-color: white;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;
}

.link{
    text-decoration: none;
}

.link:visited{
    color:black;
}
.link:hover{
    color:olive;
}

.link:active{
    color:#be9656;
}

.topLeft, .topRight{
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
}

.topCenter{
    flex: 6;
}

.logo{
    height:80px;
}

.topImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: contain;
}

.topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

.topListItem{
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.topSearchIcon{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
}